.contactsBlock {
    background-color: #917593;
}

.contactsContainer {
    flex-direction: column;
    justify-content: flex-start;
}

.titleContacts {
    background-color: #999898;
}

.contactsForm {
    width: 50%;
    background-color: #c5c2c2;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.contactsForm input, .contactsForm textarea {
    width: 100%;
}

.submitBtn {
    padding: 10px 20px;
    background-color: #e9e6e6;
}

